import DialogBox from '../Common/DialogBox'
import Link from '../Common/Link'
import Modal from '../Common/Modal'

const wikiUrl = 'https://github.com/nickcoutsos/keymap-editor/wiki/Defining-keyboard-layouts'
const layoutToolsUrl = 'https://nickcoutsos.github.io/keymap-layout-tools/'

function SuggestAutoLayoutProblems (props) {
  const wikiLink = (
    <Link href={wikiUrl} external={true}>
      Keymap Editor Wiki: Defining keyboard layouts
    </Link>
  )

  const layoutToolsLink = (
    <Link href={layoutToolsUrl} external={true}>
      Keymap Layout Tools - Helper
    </Link>
  )

  return (
    <Modal>
      <DialogBox {...props}>
        <h2>This layout was Auto-Generated</h2>
        <p>
          Hi there! Your repository didn't include a layout definition in
          the <code>info.json</code> file, so the keymap editor tried to
          generate one automatically in a last-ditch effort to help you get into
          the app.
        </p>
        <p>
          Are there any glaring issues with what you're seeing? The layout is
          based on the matrix transform defined in the keyboard's devicetree
          source, which wasn't intended for visual representation so usually
          this results in boards appearing to have an ortholinear layout and
          keys will often appear mis-aligned for a variety of reasons.
        </p>
        <p>
          If you feel this is "good enough" to get going, feel free to start
          making edits to the keymap and when you save your changes this layout
          will be saved alongside in a file named  <code>info.json</code>. You
          can make some manual edits to that file if you need to tweak some of
          the individual key positions, sizes, and even rotation. If you want a
          quicker turnaround on layout tweaks try pasting your layout into the
          (work in progress) {layoutToolsLink}.
        </p>
        <p>
          Sometimes the auto layout just isn't enough. If that's the case you
          may need to create your own layout. See {wikiLink} for instructions on
          how to get started. If you run into any trouble, reach out by making
          an issue on the project repo and I'd be happy to discuss.
        </p>
      </DialogBox>
    </Modal>
  )
}

SuggestAutoLayoutProblems.propTypes = {}

export default SuggestAutoLayoutProblems
