import find from 'lodash/find'
import map from 'lodash/map'
import { useEffect, useMemo, useState } from 'react'
import PropTypes from 'prop-types'

import github from './api'
import * as storage from './storage'
import ValidationErrors from './ValidationErrors'

import IconButton from '../../Common/IconButton'
import Selector from '../../Common/Selector'
import Spinner from '../../Common/Spinner'
import SuggestAutoLayout from '../../Dialogs/SuggestAutoLayout'
import SuggestAutoLayoutProblems from '../../Dialogs/SuggestAutoLayoutProblems'
import SuggestAutoLayoutTweak from '../../Dialogs/SuggestAutoLayoutTweak'
import SuggestRemoteRepo from '../../Dialogs/SuggestRemoteRepo'
import SuggestRepoSelection from '../../Dialogs/SuggestRepoSelection'
import SuggestWrongRepo from '../../Dialogs/SuggestWrongRepo'
import LayoutGenerationError from '../../Dialogs/LayoutGenerationError'
import ZmkNodefreeConfig from '../../Dialogs/ZmkNodefreeConfig'
import Blocked from '../../Dialogs/Blocked'

import Login from './Dialogs/Login'
import Install from './Dialogs/Install'
import KeymapUsingMiryoku from '../../Dialogs/KeymapUsingMiryoku'
import KeymapBindingsUsePreprocessorMacros from '../../Dialogs/KeymapBindingsUsePreprocessorMacros'
import KeymapParsingFailed from '../../Dialogs/KeymapParsingFailed'
import MissingRootNode from '../../Dialogs/MissingRootNode'

function GithubPicker (props) {
  const [state, setState] = useState({
    blocked: false,
    initialized: false,
    selectedRepoId: null,
    selectedBranchName: null,
    selectedKeyboard: null,
    branches: [],
    keyboards: [],
    loadingBranches: false,
    loadingKeyboard: false,
    loadError: null,
    loadWarnings: null,
    autoGenerated_unsaved: false,
    autoGenerated: false,
    showLayoutHelp: false,
    warningFlags: []
  })

  const { initialized, branches, selectedRepoId, selectedBranchName } = state
  const { keyboards, selectedKeyboard } = state
  const { loadingBranches, loadingKeyboard, loadError, loadWarnings } = state
  const { autoGenerated, autoGenerated_unsaved, showLayoutHelp } = state
  const { warningFlags } = state

  const { onSelect } = props

  const isMissingConfig = (
    loadError?.name === 'MissingRepoFile' &&
    loadError?.path === 'config'
  )

  const isMissingBoard = (
    loadError?.name === 'MissingRepoFile' &&
    loadError?.path.match(/config\/boards\/[^/]+\/[^/]+\/[^/]+?\..*$/)
  )

  const isMissingLayout = (
    loadError?.name === 'MissingRepoFile' &&
    loadError?.path === 'config/info.json'
  )

  const isLayoutGenerationFailure = (
    loadError?.name === 'LayoutGenerationError'
  )

  const isZmkNodefreeConfig = warningFlags.includes('USING_ZMK_NODEFREE_MACROS')
  const isMiryokuConfig = warningFlags.includes('USING_MIRYOKU_DTSI')
  const isUsingCustomMacros = warningFlags.includes('USING_NON_ZMK_MACRO')
  const parsingFailed = warningFlags.includes('KEYMAP_PARSING_FAILED')
  const isMissingRootNode = warningFlags.includes('MISSING_ROOT_NODE')

  const clearSelection = useMemo(() => function () {
    setState(state => ({
      ...state,
      // selectedBranchName: null,
      selectedKeyboard: null,
      loadError: null,
      loadWarnings: null,
      warningFlags: []
    }))
  }, [setState])

  const lintKeyboard = useMemo(() => function ({ layout }) {
    const noKeyHasPosition = layout.every(key => (
      key.row === undefined &&
      key.col === undefined
    ))

    if (noKeyHasPosition) {
      setState(state => ({
        ...state,
        loadWarnings: [
          'Layout in info.json has no row/col definitions. Generated keymap files will not be nicely formatted.'
        ]
      }))
    }
  }, [setState])

  const loadKeyboard = useMemo(() => async function () {
    const available = github.repositories
    const repository = find(available, { id: selectedRepoId })?.full_name
    const branch = selectedBranchName
    const keyboard = selectedKeyboard

    setState(state => ({ ...state, loadingKeyboard: true, loadError: null }))

    const response = await github.fetchLayoutAndKeymap(repository, branch, {
      keyboard
    })

    setState(state => ({
      ...state,
      keyboards: response.keyboards || [],
      loadingKeyboard: false,
      autoGenerated_unsaved: response.layoutIsAutoGenerated_unsaved,
      autoGenerated: response.layoutIsAutoGenerated,
      warningFlags: response.keymap.warningFlags || []
    }))

    lintKeyboard(response)

    onSelect({
      github: { repository, branch, keyboard },
      ...response
    })
  }, [
    selectedRepoId,
    selectedBranchName,
    selectedKeyboard,
    setState,
    lintKeyboard,
    onSelect
  ])

  useEffect(() => {
    github.init().then(() => {
      const persistedRepoId = storage.getPersistedRepository()
      const repositories = github.repositories || []
      let selectedRepoId

      if (find(repositories, { id: persistedRepoId })) {
        selectedRepoId = persistedRepoId
      } else if (repositories.length > 0) {
        selectedRepoId = repositories[0].id
      }

      setState(state => ({
        ...state,
        initialized: true,
        selectedRepoId
      }))
    })
  }, [])

  useEffect(() => {
    github.on('authentication-failed', () => {
      github.beginLoginFlow()
    })

    github.on('blocked', () => {
      setState(state => ({ ...state, blocked: true }))
    })

    github.on('repo-validation-error', err => {
      setState(state => ({
        ...state,
        loadError: err,
        loadingKeyboard: false
      }))
    })

    github.on('layout-generation-failed', err => {
      setState(state => ({
        ...state,
        loadError: err,
        loadingKeyboard: false
      }))
    })
  }, [])

  useEffect(() => {
    if (!selectedRepoId) {
      return
    }

    storage.setPersistedRepository(selectedRepoId)

    ;(async function () {
      setState(state => ({ ...state, loadingBranches: true }))

      const repository = find(github.repositories, { id: selectedRepoId })
      const branches = await github.fetchRepoBranches(repository)

      setState(state => ({
        ...state,
        branches,
        keyboard: null,
        keyboards: [],
        loadingBranches: false
      }))

      const available = map(branches, 'name')
      const defaultBranch = repository.default_branch
      const previousBranch = storage.getPersistedBranch(selectedRepoId)
      const onlyBranch = branches.length === 1 ? branches[0].name : null

      for (const branch of [onlyBranch, previousBranch, defaultBranch]) {
        if (available.includes(branch)) {
          setState(state => ({ ...state, selectedBranchName: branch }))
          break
        }
      }
    })()
  }, [selectedRepoId])

  useEffect(() => {
    if (!selectedRepoId || !selectedBranchName) {
      return
    }

    storage.setPersistedBranch(selectedRepoId, selectedBranchName)
    loadKeyboard()
  }, [selectedRepoId, selectedBranchName, loadKeyboard])

  function layoutHelp (event) {
    event.stopPropagation()
    setState(state => ({
      ...state,
      showLayoutHelp: true
    }))
  }

  if (!initialized) {
    return null
  }

  if (state.blocked) return <Blocked />

  if (!github.isGitHubAuthorized()) return <Login />

  if (github.isAppSuspendedForRepoSelection()) {
    return (
    <SuggestRepoSelection installation={github.installations[0]} />
    )
  }

  if (!github.isAppInstalled()) return <Install />

  const repositories = github.repositories.map(repo => ({
    id: repo.id,
    name: repo.full_name
  }))

  const extras = github.installations.map(installation => ({
    id: installation.id,
    type: 'installation',
    name: `[Manage available repos for ${installation.account.login}...]`
  }))

  const repositoryChoices = [...repositories, ...extras]

  const branchChoices = branches.map(branch => ({
    id: branch.name,
    name: branch.name
  }))

  const keyboardChoices = keyboards.map(keyboard => ({
    id: keyboard,
    name: keyboard
  }))

  function handleSelectRepo (id, choice) {
    if (choice.type === 'installation') {
      github.manageInstallationRepos(id)
      return
    }

    setState(state => ({
      ...state,
      selectedRepoId: id,
      selectedBranchName: null
    }))
  }

  return (
    <>
      <Selector
        id="repo"
        label="Repository"
        value={selectedRepoId}
        choices={repositoryChoices}
        onUpdate={handleSelectRepo}
      />

      {loadingBranches
        ? (
        <Spinner />
          )
        : branches.length > 1 && (
        <Selector
          id="branch"
          label="Branch"
          value={selectedBranchName}
          choices={branchChoices}
          onUpdate={name => setState(state => ({
            ...state,
            selectedBranchName: name,
            selectedKeyboard: null
          }))}
        />
        )}

      {keyboards.length > 1 && (
        <Selector
          id="keyboard"
          label="Keyboard"
          value={selectedKeyboard}
          choices={keyboardChoices}
          onUpdate={name => setState(state => ({
            ...state,
            selectedKeyboard: name
          }))}
        />
      )}

      {loadingKeyboard && <Spinner />}
      {isMissingLayout && <SuggestAutoLayout onDismiss={clearSelection} />}
      {isMissingBoard && <SuggestRemoteRepo onDismiss={clearSelection} />}
      {isMissingConfig && (
        <SuggestWrongRepo
          onDismiss={clearSelection}
          otherRepoOrBranchAvailable={
            repositoryChoices.length > 1 ||
            branchChoices.length > 0
          }
        />
      )}
      {isZmkNodefreeConfig && <ZmkNodefreeConfig onDismiss={clearSelection} />}
      {isMiryokuConfig && <KeymapUsingMiryoku onDismiss={clearSelection} />}
      {isMissingRootNode && <MissingRootNode onDismiss={clearSelection} />}
      {isLayoutGenerationFailure && <LayoutGenerationError onDismiss={clearSelection} />}
      {isUsingCustomMacros && <KeymapBindingsUsePreprocessorMacros onDismiss={clearSelection} />}
      {parsingFailed && <KeymapParsingFailed onDismiss={clearSelection} />}
      {autoGenerated && showLayoutHelp && (
        autoGenerated_unsaved
          ? <SuggestAutoLayoutProblems onDismiss={() => setState(state => ({ ...state, showLayoutHelp: false }))} />
          : <SuggestAutoLayoutTweak onDismiss={() => setState(state => ({ ...state, showLayoutHelp: false }))} />
      )}

      {/* This is getting unwieldy and I'm sorry */}
      {loadError && !isLayoutGenerationFailure && !isMissingBoard && !isMissingLayout && !isMissingConfig && (
        <ValidationErrors
          title={loadError.name}
          errors={loadError.errors}
          otherRepoOrBranchAvailable={
            repositoryChoices.length > 1 ||
            branchChoices.length > 0
          }
          onDismiss={clearSelection}
        />
      )}
      {loadWarnings && (
        <ValidationErrors
          title="Warning"
          errors={loadWarnings}
          onDismiss={() => setState(state => ({ ...state, loadWarnings: null }))}
        />
      )}

      {selectedBranchName && !loadingKeyboard && (
        <IconButton icon="sync" onClick={loadKeyboard} />
      )}

      {autoGenerated && (
        <IconButton icon="warning" onClick={layoutHelp}>
          Layout wrong?
        </IconButton>
      )}
    </>
  )
}

GithubPicker.propTypes = {
  onSelect: PropTypes.func.isRequired
}

export default GithubPicker
