import { useContext } from 'react'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'
import { SettingsContext } from '../providers'

const linkStyle = {
  color: 'royalblue',
  textDecoration: 'underline',
  cursor: 'pointer'
}

function SuggestAutoLayout (props) {
  const { openSettingsDialog } = useContext(SettingsContext)
  const settingsLink = (
    <span onClick={openSettingsDialog} style={linkStyle}>
      Settings
    </span>
  )

  return (
    <Modal>
      <DialogBox {...props}>
        <h2>Missing Layout</h2>
        <p>
          Hi there! The repository you're trying to load from doesn't have a
          defined <code>config/info.json</code> file which provides the layout
          metadata needed to render keymaps.
        </p>
        <p>
          If you don't want to make your own layout right away you can let
          Keymap Editor attempt to generate one automatically based the matrix
          transform in your zmk-config's devicetree code.
        </p>
        <p>
          See the {settingsLink} dialog for more details.
        </p>
      </DialogBox>
    </Modal>
  )
}

SuggestAutoLayout.propTypes = {}

export default SuggestAutoLayout
