import '@fortawesome/fontawesome-free/css/all.css'

import { useCallback, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import './App.css'
import { SettingsContext } from './providers'
import * as api from './api'

import IconButton from './Common/IconButton'
import Spinner from './Common/Spinner'
import Keyboard from './Keyboard/Keyboard'
import GitHubLink from './GitHubLink'
import Settings from './Dialogs/Settings'
import SourcePicker from './Sources/SourcePicker'
import github from './Sources/Github/api'
import BuildLink from './Sources/Github/BuildLink'
import Commit from './Sources/Github/Dialogs/Commit'

import ErrorBoundary from './ErrorBoundary'
import KeymapSaveFailed from './Dialogs/KeymapSaveFailed'
import { DefinitionsContextProvider } from './Contexts/Definitions'
import { LayoutContextProvider } from './Contexts/Layout'

function App () {
  const state = useSelector(state => state)
  const dispatch = useDispatch()
  const {
    source,
    sourceOther,
    keyboard,
    _unmodifiedKeymap,
    saving,
    saveError,
    theme,
    settingsVisible,
    experimentalSettings,
    latestBuild
  } = state

  // useEffect(() => {
  //   (async function () {
  //     console.log('loading devicetree parser')
  //     const { getParser } = await import('devicetree')
  //     const parser = await getParser()
  //     const tree = parser.parse('/ {};')
  //     console.log(tree, tree.rootNode.toString())
  //   })()
  // }, [])

  useEffect(() => {
    if (!sourceOther?.github) {
      dispatch({ type: 'GITHUB_BUILD_UPDATE', build: null })
      return
    }

    function handler (run) {
      if (run.head_branch === sourceOther.github.branch) {
        dispatch({ type: 'GITHUB_BUILD_UPDATE', build: run })
      }
    }

    ;(async function () {
      const { repository, branch } = sourceOther.github
      handler(await github.fetchBuildStatus(repository, branch))
    })()

    github.on('build-update', handler)
    return () => github.off('build-update', handler)
  }, [sourceOther, dispatch])

  function openCommitDialog () {
    dispatch({ type: 'COMMIT_DIALOG_OPEN' })
  }

  async function handleCompile () {
    const keyboard = sourceOther.keyboard
    const { keymap } = state.keyboard

    try {
      const updated = await api.updateKeymap(keyboard, keymap)
      dispatch({
        type: 'KEYMAP_SAVE_SUCCESS',
        keymap: updated
      })
    } catch {
      dispatch({ type: 'KEYMAP_SAVE_FAILURE' })
    }
  }

  const handleCommitChanges = useCallback(function (message) {
    const { repository, branch } = sourceOther.github

    ;(async function () {
      dispatch({ type: 'COMMIT_DIALOG_CLOSE' })
      dispatch({ type: 'KEYMAP_SAVE_START' })

      const { layout, keymap } = keyboard

      try {
        const { data: updated } = await github.commitChanges(repository, branch, layout, keymap, {
          message, experimentalSettings, keyboard: sourceOther.github.keyboard
        })
        dispatch({ type: 'KEYMAP_SAVE_SUCCESS', keymap: updated })
      } catch {
        dispatch({ type: 'KEYMAP_SAVE_FAILURE' })
      }
    })()
  }, [sourceOther, keyboard, dispatch, experimentalSettings])

  const handleKeyboardSelected = useCallback(function (event) {
    const { source, layout, keymap, sensors = [], ...other } = event
    dispatch({
      type: 'SELECT_KEYBOARD',
      source,
      sourceOther: other,
      layout,
      keymap,
      sensors
    })
  }, [dispatch])

  const handleUpdateSettings = useCallback(function (settings) {
    dispatch({ type: 'SETTINGS_UPDATE', settings })
  }, [dispatch])

  const openSettingsDialog = useCallback(
    () => dispatch({ type: 'SETTINGS_OPEN' }),
    [dispatch]
  )

  const hasChanged = keyboard && !Object.is(keyboard.keymap, _unmodifiedKeymap)

  return (
    <>
      <ErrorBoundary>
        <SettingsContext.Provider value={{
          settings: experimentalSettings,
          openSettingsDialog
        }}>
          <SourcePicker onSelect={handleKeyboardSelected} />

          <div id="settings-bar">
            <IconButton
              id="light-mode"
              title="Toggle theme"
              icon={{
                light: 'sun',
                dark: 'moon',
                system: 'circle-half-stroke'
              }[theme] || 'circle-half-stroke'}
              onClick={() => dispatch({ type: 'CYCLE_THEME' })}
            />

            <IconButton
              id="settings-button"
              icon="cog"
              onClick={openSettingsDialog}
            />
            {settingsVisible && (
              <Settings
                settings={experimentalSettings}
                onUpdate={handleUpdateSettings}
                onClose={() => dispatch({ type: 'SETTINGS_CLOSE' })}
              />
            )}
          </div>

          <div id="actions">
            {source === 'local' && (
              <button disabled={!hasChanged} onClick={handleCompile}>
                Save Local
              </button>
            )}
            {source === 'github' && (
              <button
                title="Commit keymap changes to GitHub repository"
                disabled={!hasChanged}
                onClick={openCommitDialog}
              >
                {saving ? 'Saving' : 'Commit Changes'}
                {saving && <Spinner />}
              </button>
            )}
            {state.commitDialogOpen && (
              <Commit
                onConfirm={handleCommitChanges}
                onCancel={() => dispatch({ type: 'COMMIT_DIALOG_CLOSE' })}
              />
            )}

            {latestBuild && <BuildLink latestRun={latestBuild} />}
          </div>
          <DefinitionsContextProvider>
            {keyboard && (
              <LayoutContextProvider layout={keyboard.layout}>
                <Keyboard
                  keymap={keyboard.keymap}
                  sensors={keyboard.sensors}
                />
              </LayoutContextProvider>
            )}
            {saveError && (
              <KeymapSaveFailed onDismiss={() => dispatch({ type: 'CLEAR_SAVE_ERROR' })} />
            )}
          </DefinitionsContextProvider>
        </SettingsContext.Provider>
      </ErrorBoundary>
      <GitHubLink />
    </>
  )
}

export default App
