import groupBy from 'lodash/groupBy'
import map from 'lodash/map'

import reservedIdentifiers from 'zmk-data/data/extracted-data/zmk-reserved-identifiers.json'

export function sanityCheck (keymap) {
  return {
    nodeNames: sanityCheckNodeNames(keymap),
    nodeLabels: sanityCheckNodeLabels(keymap),
    nodeReferences: sanityCheckNodeReferences(keymap)
  }
}

function getDuplicatesBy (collection, key) {
  const itemsByKey = groupBy(collection, key)
  return Object.values(itemsByKey)
    .filter(group => group.length > 1)
}

function doesNameSuggestPassword (name) {
  const patterns = [
    /(^|[\b_])pass(word)?([\b_]|$)/
  ]
  return patterns.some(pattern => name.match(pattern))
}

function sanityCheckNodeNames (keymap) {
  const layerNames = getLayerNodeNames(keymap.layers)
  const comboNames = getComboNodeNames(keymap.combos)
  const macroNames = getMacroNodeNames(keymap.macros)
  const behaviorNames = getBehaviorNodeNames(keymap.behaviors)

  const duplicates = [
    ...getDuplicatesBy(layerNames, 'name'),
    ...getDuplicatesBy(comboNames, 'name'),
    ...getDuplicatesBy(macroNames, 'name'),
    ...getDuplicatesBy(behaviorNames, 'name')
  ]

  const redefined = [
    ...layerNames.filter(layer => reservedIdentifiers.includes(layer.name)),
    ...comboNames.filter(combo => reservedIdentifiers.includes(combo.name)),
    ...macroNames.filter(macro => reservedIdentifiers.includes(macro.name)),
    ...behaviorNames.filter(behavior => reservedIdentifiers.includes(behavior.name))
  ]

  const passwordMacros = [
    ...macroNames.filter(macro => doesNameSuggestPassword(macro.name))
  ]

  return { duplicates, redefined, passwordMacros }
}

function sanityCheckNodeLabels (keymap) {
  const duplicates = getDuplicatesBy([].concat(
    getLayerLabels(keymap.layers),
    getMacroLabels(keymap.macros),
    getBehaviorLabels(keymap.behaviors)
  ), 'label')

  return { duplicates }
}

function sanityCheckNodeReferences (keymap) {
  const nodesByRef = groupBy([].concat(
    getMacroReferences(keymap.macros),
    getBehaviorReferences(keymap.behaviors)
  ), 'ref')

  const duplicates = Object.values(nodesByRef).reduce((duplicates, nodes) => {
    if (nodes.length > 1) {
      duplicates.push(map(nodes, 'path'))
    }

    return duplicates
  }, [])

  return { duplicates }
}

function getLayerNodeNames (layers) {
  return layers.map((layer, i) => ({
    location: `Keymap - Layer #${i} (${layer.name})`,
    path: `keymap.layers[${i}]`,
    name: layer.name
  }))
}

function getLayerLabels (layers) {
  return layers.reduce((labels, layer, i) => {
    if (layer.label) {
      labels.push({
        location: `Keymap - Layer #${i} (${layer.name})`,
        path: `keymap.layers[${i}].label`,
        label: layer.label
      })
    }

    return labels
  }, [])
}

function getComboNodeNames (combos) {
  return combos.map((combo, i) => ({
    location: `Combos - Combo #${i} (${combo.identifier})`,
    path: `combos[${i}].name`,
    name: combo.identifier
  }))
}

function getMacroNodeNames (macros) {
  return macros.map((macro, i) => ({
    location: `Macros - Macro #${i} (${macro.identifier})`,
    path: `macros[${i}].name`,
    name: macro.identifier
  }))
}

function getMacroLabels (macros) {
  return macros.reduce((labels, macro, i) => {
    if (macro.label) {
      labels.push({
        location: `Macros - Macro #${i} (${macro.identifier}) - Label`,
        path: `macros[${i}].label`,
        label: macro.label
      })
    }

    return labels
  }, [])
}

function getMacroReferences (macros) {
  return macros.map((macro, i) => ({
    location: `Macros > Macro #${i} (${macro.identifier})`,
    path: `macros[${i}].name`,
    ref: macro.identifier
  }))
}

function getBehaviorNodeNames (behaviors) {
  return behaviors.map((behavior, i) => ({
    location: `Behaviors - Behavior #${i} (${behavior.labels[0]}) - Name`,
    path: `behaviors[${i}].name`,
    name: behavior.name
  }))
}

function getBehaviorLabels (behaviors) {
  return behaviors.reduce((labels, behavior, i) => {
    if (behavior.label) {
      labels.push({
        location: `Behaviors - Behavior #${i} (${behavior.labels[0]}) - Label`,
        path: `behaviors[${i}].label`,
        label: behavior.properties.label.parsed
      })
    }

    return labels
  }, [])
}

function getBehaviorReferences (behaviors) {
  return behaviors.map((behavior, i) => ({
    location: `Behaviors - Behavior #${i} (${behavior.labels[0]}) - Bind`,
    path: `behaviors[${i}].bind`,
    ref: behavior.labels[0]
  }))
}
