import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'

import { isControlBinding } from './util'
import styles from './styles.module.css'
import { DropZoneContainer } from './DragAndDrop'
import SequenceModal from './SequenceModal'

import Key from '../Keys/Key'
import KeyWrapper from '../Keys/KeyWrapper'

import ActionButton from '../../Common/ActionButton'
import Icon from '../../Common/Icon'
import { stop } from '../../event-util'

import {
  addMacroControlBinding,
  addMacroKeyBinding,
  deleteMacroBinding,
  reorderMacroBinding, updateMacroBinding
} from './macrosSlice'

function MacroBinds (props) {
  const { macroIndex, bindings, dispatch } = props
  const [dragging, setDragging] = useState(null)
  const [sequenceModalOpen, setSequenceModalOpen] = useState(false)

  function handleDragStart (event, index) {
    if (event.dataTransfer.items.length) {
      event.dataTransfer.clearData()
      return
    }

    const binding = bindings[index]
    setDragging({
      index,
      mode: isControlBinding(binding) ? 'row' : 'col',
      isControlBinding: isControlBinding(binding)
    })
  }

  useEffect(() => {
    function clearDrop () {
      setDragging(null)
    }
    function clearDrag (event) {
      event.stopPropagation()
      event.preventDefault()
    }

    document.body.addEventListener('drop', clearDrop)
    document.body.addEventListener('dragover', clearDrag)
    return () => {
      document.body.removeEventListener('drop', clearDrop)
      document.body.removeEventListener('dragover', clearDrag)
    }
  }, [setDragging])

  function handleDrop (targetIndex) {
    dispatch(reorderMacroBinding({
      macroIndex,
      bindIndex: dragging.index,
      targetIndex
    }))
    setDragging(null)
  }

  return (
    <>
      <div className={styles.macroBindings}>
        {bindings.map((bind, i) => (
          <React.Fragment key={i}>
            <KeyWrapper
              key={i}
              data-index={i}
              themeClass={isControlBinding(bind) ? styles.controlBinding : ''}
              draggable
              onDragStart={e => handleDragStart(e, i)}
            >
              <Key
                context={isControlBinding(bind) ? 'macros' : undefined}
                value={bind.value}
                params={bind.params}
                onUpdate={updated => {
                  dispatch(updateMacroBinding({
                    macroIndex,
                    bindIndex: i,
                    binding: updated
                  }))
                }}
              />
              <div className={styles.actions}>
                <Icon
                  name="trash"
                  className={styles.deleteBind}
                  onClick={stop(() => dispatch(deleteMacroBinding({
                    macroIndex,
                    bindIndex: i
                  })))}
                />
              </div>
            </KeyWrapper>
          </React.Fragment>
        ))}
        <DropZoneContainer
          dragging={dragging}
          bindings={bindings}
          onDrop={handleDrop}
        />
      </div>

      {sequenceModalOpen && (
        <SequenceModal onClose={bindings => {
          setSequenceModalOpen(false)
          for (const binding of bindings) {
            dispatch(addMacroKeyBinding({
              macroIndex,
              binding
            }))
          }
        }} />
      )}

      <div className={styles.macroActions}>
        <ActionButton onClick={() => dispatch(addMacroControlBinding({ macroIndex }))}>
          Add Control Binding
        </ActionButton>
        <ActionButton onClick={() => {
          dispatch(addMacroKeyBinding({
            binding: { value: '&kp', params: [] },
            macroIndex
          }))
        }}>
          Add Key Binding
        </ActionButton>
        <ActionButton onClick={() => setSequenceModalOpen(true)}>
          Add String Sequence
        </ActionButton>
      </div>
    </>
  )
}

MacroBinds.propTypes = {
  bindings: PropTypes.array.isRequired,
  macroIndex: PropTypes.number.isRequired,
  dispatch: PropTypes.func.isRequired
}

export default MacroBinds
