import PropTypes from 'prop-types'
import { useContext, useMemo } from 'react'

import { SearchContext } from '../providers'
import ResultList from './ResultList'
import style from './style.module.css'

function LayerPicker (props) {
  const { getSearchTargets } = useContext(SearchContext)
  const { onSelect } = props

  const choices = useMemo(() => getSearchTargets('layer'), [getSearchTargets])

  return (
    <div className={style.dialog}>
      <p>Select layer</p>
      <ResultList
        results={choices}
        onSelect={onSelect}
        renderResult={result => (
          <>
            {result.code}
            {result.description && <p className={style.description}>{result.description}</p>}
          </>
        )}
      />
    </div>
  )
}

LayerPicker.propTypes = {
  value: PropTypes.string.isRequired,
  behaviour: PropTypes.object.isRequired,
  onSelect: PropTypes.func.isRequired
}

LayerPicker.defaultProps = {
  value: ''
}

export default LayerPicker
