import { useCallback, useEffect, useMemo, useState } from 'react'

import * as api from '../../api'
import Selector from '../../Common/Selector'
import KeymapBindingsUsePreprocessorMacros from '../../Dialogs/KeymapBindingsUsePreprocessorMacros'
import KeymapUsingMiryoku from '../../Dialogs/KeymapUsingMiryoku'
import MissingRootNode from '../../Dialogs/MissingRootNode'
import ZmkNodefreeConfig from '../../Dialogs/ZmkNodefreeConfig'

function LocalPicker ({ onSelect }) {
  const [keyboards, setKeyboards] = useState([])
  const [keyboard, setKeyboard] = useState(null)
  const [warningFlags, setWarningFlags] = useState([])

  const choices = useMemo(() => (
    keyboards.map(name => ({ id: name, name }))
  ), [keyboards])

  useEffect(() => {
    (async function () {
      const keyboards = await api.loadAvailableKeyboards()
      const persistedKeyboard = JSON.parse(localStorage.getItem('localSourceSelectedKeyboard') || 'null')
      const initialSelection = keyboards.includes(persistedKeyboard)
        ? persistedKeyboard
        : keyboards[0]

      setKeyboards(keyboards)
      setKeyboard(initialSelection)
    })()
  }, [setKeyboards, setKeyboard])

  useEffect(() => {
    (async function () {
      if (keyboard) {
        const { layout, keymap, sensors } = await api.loadKeyboardFiles(keyboard)
        if (keymap.warningFlags?.length) {
          setWarningFlags(keymap.warningFlags)
          return
        }

        onSelect({
          source: 'local',
          keyboard,
          layout,
          keymap,
          sensors
        })
      }
    })()
  }, [onSelect, setWarningFlags, keyboard])

  const handleSelectKeyboard = useCallback(keyboard => {
    localStorage.setItem('localSourceSelectedKeyboard', JSON.stringify(keyboard))
    setKeyboard(keyboard)
  }, [setKeyboard])

  const isMissingRootNode = warningFlags.includes('MISSING_ROOT_NODE')
  const isZmkNodefreeConfig = warningFlags.includes('USING_ZMK_NODEFREE_MACROS')
  const isMiryokuConfig = warningFlags.includes('USING_MIRYOKU_DTSI')
  const isUsingCustomMacros = warningFlags.includes('USING_NON_ZMK_MACRO')

  const clearSelection = useCallback(() => {
    setKeyboard(null)
    setWarningFlags([])
  }, [setKeyboard, setWarningFlags])

  if (keyboards.length < 2) {
    return null
  }

  if (isZmkNodefreeConfig) return <ZmkNodefreeConfig onDismiss={clearSelection} />
  if (isMiryokuConfig) return <KeymapUsingMiryoku onDismiss={clearSelection} />
  if (isUsingCustomMacros) return <KeymapBindingsUsePreprocessorMacros onDismiss={clearSelection} />
  if (isMissingRootNode) return <MissingRootNode onDismiss={clearSelection} />

  return (
    <Selector
      id="keyboard"
      label="Keyboard"
      value={keyboard}
      choices={choices}
      onUpdate={handleSelectKeyboard}
    />
  )
}

export default LocalPicker
