import { createSlice } from '@reduxjs/toolkit'

import { repositionElement } from '../../reducer-util'

export const selectMacros = state => state.keyboard.keymap.macros

const macrosSlice = createSlice({
  name: 'macros',
  initialState: [],
  reducers: {
    addMacro (state) {
      state.push({
        identifier: 'new_macro',
        label: 'new_macro',
        bindings: []
      })
    },
    renameMacro (state, action) {
      state[action.payload.index].identifier = action.payload.name
    },
    deleteMacro (state, action) {
      return state.splice(action.payload.index, 1)
    },
    addMacroControlBinding (state, action) {
      state[action.payload.macroIndex].bindings.push({
        value: '&macro_press',
        params: []
      })
    },
    addMacroKeyBinding (state, action) {
      state[action.payload.macroIndex].bindings.push(action.payload.binding)
    },
    updateMacroBinding (state, action) {
      state[action.payload.macroIndex].bindings[action.payload.bindIndex] = action.payload.binding
    },
    reorderMacroBinding (state, action) {
      state[action.payload.macroIndex].bindings = repositionElement(
        state[action.payload.macroIndex].bindings,
        action.payload.bindIndex,
        action.payload.targetIndex
      )
    },
    deleteMacroBinding (state, action) {
      state[action.payload.macroIndex].bindings.splice(action.payload.macroIndex, 1)
    },
    customizeMacroOptions (state, action) {
      Object.assign(state[action.payload.macroIndex], action.payload.options)
    }
  }
})

export const {
  addMacro,
  renameMacro,
  deleteMacro,
  addMacroControlBinding,
  addMacroKeyBinding,
  updateMacroBinding,
  reorderMacroBinding,
  deleteMacroBinding,
  customizeMacroOptions
} = macrosSlice.actions

export default macrosSlice.reducer
