import PropTypes from 'prop-types'
import { useContext, useState } from 'react'

import MacroBinds from './MacroBinds'
import ConfigModal from './ConfigModal'
import styles from './styles.module.css'
import EditableField from '../../Common/EditableField'
import Icon from '../../Common/Icon'
import { validateDevicetreeLabel } from '../validators'
import { SearchContext } from '../../providers'
import {
  customizeMacroOptions,
  deleteMacro,
  renameMacro
} from './macrosSlice'

function Macro (props) {
  const { index, macro, dispatch } = props
  const [showConfig, setShowConfig] = useState(false)
  const { identifiers } = useContext(SearchContext)

  function validateMacroIdentifier (value) {
    return validateDevicetreeLabel(value) || (
      identifiers.includes(value)
        ? `Cannot use existing identifier "${value}"`
        : null
    )
  }

  function handleUpdateConfig (options) {
    setShowConfig(false)
    dispatch(customizeMacroOptions({ macroIndex: index, options }))
  }

  return (
    <div className={styles.macro}>
      {showConfig && (
        <ConfigModal macro={macro} onUpdate={handleUpdateConfig} />
      )}
      <div className={styles.heading}>
        <EditableField
          value={macro.identifier}
          validate={validateMacroIdentifier}
          render={children => <h5><code>{children}</code></h5>}
          onUpdate={name => dispatch(renameMacro({ index, name }))}
        />
        <div>
          <Icon
            name="wrench"
            className={`${styles.action}`}
            onClick={() => setShowConfig(true)}
          />
          <Icon
            name="trash-can"
            className={`${styles.action} ${styles.remove}`}
            onClick={() => dispatch(deleteMacro({ index }))}
          />
        </div>
      </div>
      <MacroBinds
        macroIndex={index}
        bindings={macro.bindings}
        dispatch={dispatch}
      />
    </div>
  )
}

Macro.propTypes = {
  index: PropTypes.number.isRequired,
  macro: PropTypes.shape({
    identifier: PropTypes.string,
    bindings: PropTypes.array.isRequired
  }).isRequired,
  dispatch: PropTypes.func.isRequired
}

export default Macro
