const singleValue = value => ({ value: '&kp', params: [{ value, params: [] }] })

const patternMappings = [
  { pattern: /^[0-9]$/, convert: digit => singleValue(`N${digit}`) },
  { pattern: /^[a-z]$/, convert: char => singleValue(char.toUpperCase()) },
  {
    pattern: /^[A-Z]$/,
    convert: char => ({
      value: '&kp',
      params: [{
        value: 'LS',
        params: [{
          value: char.toUpperCase(),
          params: []
        }]
      }]
    })
  }
]

const simpleMappings = {
  '!': singleValue('EXCL'),
  '@': singleValue('AT'),
  '#': singleValue('POUND'),
  '$': singleValue('DLLR'),
  '%': singleValue('PRCNT'),
  '^': singleValue('CARET'),
  '&': singleValue('AMPS'),
  '*': singleValue('STAR'),
  '(': singleValue('LPAR'),
  ')': singleValue('RPAR'),
  '\n': singleValue('RET'),
  ' ': singleValue('SPACE'),
  '-': singleValue('MINUS'),
  '_': singleValue('UNDER'),
  '=': singleValue('EQUAL'),
  '+': singleValue('PLUS'),
  '[': singleValue('LBKT'),
  ']': singleValue('LBRC'),
  '{': singleValue('RBKT'),
  '}': singleValue('RBRC'),
  '\\': singleValue('BSLH'),
  '|': singleValue('PIPE'),
  '': singleValue('TILDE2'),
  ';': singleValue('SEMI'),
  ':': singleValue('COLON'),
  '\'': singleValue('SQT'),
  '"': singleValue('DQT'),
  '`': singleValue('GRAVE'),
  '~': singleValue('TILDE'),
  ',': singleValue('COMMA'),
  '<': singleValue('LT'),
  '.': singleValue('DOT'),
  '>': singleValue('GT'),
  '/': singleValue('FSLH'),
  '?': singleValue('QMARK')
}

function mapCharacter (char) {
  if (simpleMappings[char]) {
    return simpleMappings[char]
  }

  for (const { pattern, convert } of patternMappings) {
    if (char.match(pattern)) {
      return convert(char)
    }
  }
}

module.exports = {
  mapCharacter
}
