import ReactDOM from 'react-dom'
import { useCallback, useRef } from 'react'
import PropTypes from 'prop-types'

const styles = {
  wrapper: {
    position: 'absolute',
    top: '0',
    left: '0',
    width: '100vw',
    height: '100vh',
    backgroundColor: 'rgba(104, 123, 162, 0.39)',
    backdropFilter: 'blur(1px)',
    zIndex: '50',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
  },
  centerOnly: {
    background: 'transparent',
    pointerEvents: 'none'
  },
  content: {
    display: 'block',
    pointerEvents: 'all'
  }
}

function Modal ({ onDismiss, centerOnly = false, children }) {
  const content = useRef()
  const handleClick = useCallback(function (e) {
    if (onDismiss && content.current && e.target.contains(content.current)) {
      onDismiss()
    }
  }, [content, onDismiss])

  const style = centerOnly
    ? { ...styles.wrapper, ...styles.centerOnly }
    : styles.wrapper

  return ReactDOM.createPortal(
    <div onClick={handleClick} style={style}>
      <div ref={content} style={styles.content}>
        {children}
      </div>
    </div>,
    document.getElementById('modal-root')
  )
}

Modal.propTypes = {
  onDismiss: PropTypes.func
}

export default Modal
