import pick from 'lodash/pick'
import { useState } from 'react'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import styles from './styles.module.css'

function ConfigModal ({ macro, onUpdate }) {
  const [pending, setPending] = useState(pick(macro, [
    'label',
    'waitMs',
    'tapMs'
  ]))

  function handleConfirm () {
    onUpdate(pending)
  }

  return (
    <Modal>
      <DialogBox onDismiss={handleConfirm}>
        <h2>Customize [<code>{macro.identifier}</code>]</h2>
        <div>

          <div className={styles.field}>
            <label htmlFor="customize-macro-label">
              Label
            </label>
            <input
              id="customize-macro-label"
              value={pending.label}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  label: event.target.value
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="customize-macro-wait">
              Wait (ms)
            </label>
            <input
              id="customize-macro-wait"
              type="number"
              placeholder="Default"
              value={pending.waitMs}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  waitMs: event.target.value === ''
                    ? undefined
                    : Number(event.target.value)
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="customize-macro-tap">
              Tap (ms)
            </label>
            <input
              id="customize-macro-tap"
              type="number"
              placeholder="Default"
              value={pending.tapMs}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  tapMs: event.target.value === ''
                    ? undefined
                    : Number(event.target.value)
                }))
              )}
            />
          </div>

        </div>
      </DialogBox>
    </Modal>
  )
}

export default ConfigModal
